
.navbar[data-v-1a71fa89]{
  border-radius: 0px !important;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index:10;
  background-color: #24292E;
  border-style: none;
}
.tab[data-v-1a71fa89]{
  color: gray;
  height: 50px;
  line-height: 50px;
  padding-left: 10px;
  padding-right: 10px;
  cursor: pointer;
  -webkit-transition: color 0.5s ease;
  transition: color 0.5s ease;
  font-weight: 600;
  font-size: 14px;
  font-family: "Segoe UI";
}
.tab-active[data-v-1a71fa89]{
  color: whitesmoke;
  border-bottom-style: solid;
  border-bottom-width: 5px;
  border-bottom-color: #79AB43;
}

/* Home at Top Left */
.home[data-v-1a71fa89]{
  margin-top: 14px;
  margin-left: 10px;
  font-size: 1.2em;
  cursor: pointer;
}
.green[data-v-1a71fa89]{
  color: #79AB43;
  font-family: 'Segoe UI';
  margin-top: 5px;
}
.white[data-v-1a71fa89]{
  color: whitesmoke;
  font-family: 'Segoe UI';
}




